<template>
  <div class="sign-in-form-molecule">
    <h1>Sign In</h1>
    <p>Please enter your email and password</p>
    <div class="sign-in-form-molecule__form">
      <el-alert v-if="signInError" :title="signInError" type="error" effect="dark" :closable="false" show-icon></el-alert>
      <div class="sign-in-form-molecule__form__inputs">
        <el-input placeholder="Email" v-model="email"></el-input>
        <el-input placeholder="Password" v-model="password" show-password></el-input>
      </div>

      <div class="sign-in-form-molecule__form__buttons">
        <el-button type="primary" v-on:click="submit">Sign In</el-button>
      </div>

    </div>
  </div>
</template>

<script>
import SignInFormMolecule from './SignInFormMolecule.hook'
const hook = new SignInFormMolecule();

export default {
  name: 'SignInFormMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("submit"),
  props: {},
  data(){
      return {
          email:undefined,
          password:undefined,
          signInError: undefined
      }
  }
}

</script>

<style scoped lang="scss">
  .sign-in-form-molecule {
    &__form {
      
      &__inputs {
        margin-top:30px;
        margin-bottom:30px;
        .el-input {
          margin-bottom: 10px;
        }
      }

      &__buttons {
        margin-bottom:30px;
        .el-button {
          width:120px;
          margin-right:20px;
        }
      }
    }
  }
</style>
